import React from "react";
import PlayCircleFilledIcon from "@mui/icons-material/PlayCircleFilled";
import Navbar from "../component/Navbar";
import Navbar3 from "../component/Navbar3";
import { useNavigate } from "react-router-dom";

export const MoviesBanner = () => {
  const navigate = useNavigate()
  const [obj, setObj] = React.useState({
    id: 1,
    title: "Panipat : The Great Betrayal",
    category: "Bollywood",
    bannerPic:
      "https://occ-0-6246-2164.1.nflxso.net/dnm/api/v6/6AYY37jfdO6hpXcMjf9Yu5cnmO0/AAAABQ25sbcXndX5Mn93hJ2mnQS81972TBoTVo1xJhMCs7SPh9H2VKtEDIWEOH9DfNJ1TuoRNfFxAaAKrndOJEWna_oqFTYc.webp?r=1d4",
    namePic:
      "https://occ-0-6246-2164.1.nflxso.net/dnm/api/v6/LmEnxtiAuzezXBjYXPuDgfZ4zZQ/AAAABaiBEtZppLB3RDCapQ314ODZ0gNEus-9ja_eYTxO5NWC0j07gMZbvn45rStaK2fKe7DrSq9AP1cNAnQrfnSoQOPFOm1Of_dS3jx-.webp?r=644",
    description:
      "In 18th-century India, a Maratha commander leads his army in preparation for a fierce battle against Afghan invaders in this film based on real events.",
    match: 93,
    certificate: "U/A 16+",
    duration: "2h 50m",
    year: 2019,
    cast: ["Arjun Kapoor", "Sanjay Dutt", "Kriti Sanon", "more"],
    genres: [
      "Military Movies",
      "Movies Based on Real Life",
      "Hindi-Language Movies",
    ],
    quality: ["Emotional", "Exciting", "Romantic"],
  });

  React.useEffect(() => {
    let id = 2;
    setInterval(() => {
      console.log(id);
      fetch(`http://localhost:3000/banners-mo/${id}`)
        .then((res) => res.json())
        .then((res) => setObj(res))
        .catch((err) => console.log(err));
      if (id >= 4) {
        id = 1;
      } else {
        id++;
      }
    }, 10000);
  }, []);

  return (
    <div
      style={{
        backgroundImage: `url("${obj.bannerPic}")`,
        height: "90vh",
        width: "100%",
        backgroundPosition: "center",
        backgroundSize: "cover",
      }}
    >
      <Navbar />
      <Navbar3 />
      <div style={{ width: "35%" }}>
        <img
          style={{ marginTop: "30%", marginLeft: "15%", width: "100%" }}
          src={`${obj.namePic}`}
          alt=""
        />

        <h3
          style={{
            color: "#fff",
            fontWeight: "400",
            lineHeight: "normal",
            width: "100%",
            fontSize: "1.4vw",
            textShadow: "2px 2px 4px rgb(0 0 0 / 45%)",
            marginLeft: "15%",
          }}
        >
          {obj.description}
        </h3>
        <div
          style={{
            marginTop: "1.5vw",
            whiteSpace: "nowrap",
            display: "flex",
            columnGap: "1rem",
            lineHeight: "88%",
            marginLeft: "15%",
          }}
        >
          <button
            onClick={() => navigate("/player")}
            style={{
              backgroundColor: "rgba(255,255,255,0.75)",
              paddingLeft: "1rem",
              paddingRight: "1rem",
              paddingTop: "4px",
              width: "35%",
              paddingBottom: "4px",
              border: "none",
              borderRadius: "3px",
              fontSize: "22px",
              fontWeight: "550",
            }}
          >
            {" "}
            <div>
              <PlayCircleFilledIcon sx={{ fontSize: "22px" }} />{" "}
              <span style={{ marginTop: "-2em" }}>Play</span>
            </div>
          </button>
          <button
            style={{
              backgroundColor: "rgba(109,109,110,0.7)",
              color: "white",
              fontSize: "22px",
              paddingRight: "1rem",
              paddingLeft: "1rem",
              border: "0px",

              width: "35%",
              borderRadius: "2px",
            }}
          >
            More Info
          </button>
        </div>
      </div>
    </div>
  );
};
